import { useContext, useEffect, useRef, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { Box, Button } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext'
import QuoteArea from './quoteArea'
import Slider from './slider'
import Attribution from './attribution'
import SuccessDisplay from './successDisplay'
import Progress from './progress'

const GameBoard = (props) => {
  const hudTopRef = useRef()
  const [root] = useContext(RootStoreContext)

  useEffect(() => {
    root.puzzleStore.loadRandomPuzzle()
  }, [root.puzzleStore])

  const [sliderYTranslate, setSliderYTranslate] = useState(0)

  useEffect(() => {
    const getSliderYTranslate = () => {
      if (
        false &&
        hudTopRef.current &&
        root.gameStore.puzzleState === 'playing'
      ) {
        const hudTopHeight = hudTopRef.current.offsetHeight
        const sentenceHeight = document.getElementById('sentence').offsetHeight
        const halfParentHeight =
          document.getElementById('top-layout').offsetHeight * 0.5 - 32
        return halfParentHeight - 0.5 * sentenceHeight - hudTopHeight
      } else {
        return root.gameStore.adSpaceHeight
      }
    }

    if (root.gameStore.puzzleState !== 'puzzleLoaded') {
      setSliderYTranslate(getSliderYTranslate())
    }
  }, [root.gameStore.puzzleState])

  return (
    <Slider
      translateAmount={sliderYTranslate}
      isTranslated={root.gameStore.puzzleState !== 'succeeded'}
    >
      <Box sx={{ width: '95%', margin: '0 auto' }}>
        <Box
          id='hudTop'
          ref={hudTopRef}
          sx={{
            /* todo: move these styles to code */
            display: 'flex',
            flexDirection: 'row-reverse',
            justifyContent: 'space-between',
            flexWrap: 'nowrap',
            //            height: '26.5px',
          }}
        >
          <Progress id='scoreBar' />
        </Box>
        <QuoteArea id='quote' />
        <Box
          sx={{
            width: '100%',
          }}
        >
          {root.gameStore.puzzleState === 'playing' &&
            root.puzzleStore.current.hintsLeft !== 0 && (
              <Box sx={{ mt: 2 }}>
                <Button
                  variant='outlined'
                  disabled={!root.gameStore.isWordSelected}
                  sx={{}}
                  onClick={() => root.gameStore.setDoRevealSelectedWord(true)}
                >
                  Reveal word ({root.puzzleStore.current.hintsLeft} left)
                </Button>
              </Box>
            )}

          <Attribution
            show={root.gameStore.puzzleState === 'succeeded'}
            html={root.gameStore.attributionHtml}
          />
        </Box>

        {root.gameStore.puzzleState === 'succeeded' && <SuccessDisplay />}
      </Box>
    </Slider>
  )
}

export default observer(GameBoard)
