import { useContext, useState } from 'react'
import * as React from 'react'
import { Box, Typography, Button, Fade } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext.js'
import Applause from './applause'
import ScoreReport from './scoreReport'
import Delayed from './delayed'

const SuccessDisplay = (props) => {
  const {
    applauseDelay = 2000,
    showStats = true,
    message = '',
    onApplauseEnter = () => {},
    onApplauseComplete = () => {},
    fadeOut = true,
  } = props

  const [root] = useContext(RootStoreContext)
  const [applause, setApplause] = useState(true)

  return (
    <Box sx={{ mt: 2, width: '100%', textAlign: 'center' }}>
      <Fade
        in={applause}
        timeout={{ enter: 900, exit: 100 }}
        exit={false}
        onEntered={(ev) => {
          setTimeout(() => {
            if (fadeOut) {
              setApplause(false)
            }
          }, applauseDelay)
          onApplauseEnter()
        }}
        onExited={(ev) => {
          ev.style.display = fadeOut ? 'none' : 'block'
          onApplauseComplete()
        }}
      >
        <Box>
          <Applause />
          <Typography variant='h5'>{message}</Typography>
        </Box>
      </Fade>
      {showStats && (
        <Delayed waitBeforeShow={500}>
          <Fade in={!applause} appear={true} timeout={1000}>
            <Box>
              <ScoreReport />
              <Button
                variant='outlined'
                sx={{}}
                onClick={() => root.puzzleStore.loadRandomPuzzle()}
              >
                next
              </Button>
            </Box>
          </Fade>
        </Delayed>
      )}
    </Box>
  )
}

export default SuccessDisplay
