import ReactGA from 'react-ga4'

const sendEvent = (cat, action, label, value) => {
  ReactGA.event({
    category: cat,
    action: action,
    label: label,
    value: value, // must be a number
    // nonInteraction: true, // optional, true/false
    // transport: 'xhr', // optional, beacon/xhr/image
  })
}

export { sendEvent }
