import { Stack, Box } from '@mui/material'

const Slider = (props) => {
  const sliderStyle = {
    width: ['98%', '95%', '90%'],
    alignItems: 'center',
    margin: '0 auto',
    // this is a hack for the fact that the component is getting
    // rendered twice, and the first time through the toptext ref
    // is null, so the translateAmount is undefined. It appears first at
    // y = zero, then a few frames later at the translated position
    display: props.translateAmount !== undefined ? 'block' : 'none',
    overflow: 'auto',
    overscrollBehavior: 'contain',
  }

  const innerBoxStyle = {
    transition: props.isTranslated ? '' : 'margin-top 1000ms ease-in-out',
    marginTop: `${props.translateAmount}px`,
  }

  return (
    <Box id='slider' alignItems='center' spacing={0} sx={sliderStyle}>
      <Box sx={innerBoxStyle}>{props.children}</Box>
    </Box>
  )
}

export default Slider
