import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext'
import { Box, Link, Typography } from '@mui/material'
import Menu from './menu'
import HelpPopup from './helpPopup'

const BottomBar = (props) => {
  const [root] = useContext(RootStoreContext)

  const outerBarStyle = {
    position: 'fixed',
    bottom: 0,
    left: 0,
    right: 0,
    height: '45px',
    width: '100%',
  }
  const innerBarStyle = {
    position: 'relative',
    fontSize: '12pt',
    borderTop: '1px solid gray',
    width: '600px',
    maxWidth: '100vw',
    left: '50%',
    transform: 'translateX(-50%)',
  }

  const singleLabelStyle = {
    textAlign: 'center',
    mt: 1,
    width: '100%',
  }

  const dailyLabelStyle = {
    textAlign: 'right',
    mt: 1,
    position: 'absolute',
    right: 'calc(50% + 15px)',
    fontWeight: root.gameStore.page === 'daily' ? 800 : 400,
  }

  const centerPipeStyle = {
    position: 'absolute',
    mt: 1,
    left: '50%',
    transform: 'translateX(-50%)',
  }

  const practiceLabelStyle = {
    textAlign: 'left',
    mt: 1,
    position: 'absolute',
    left: 'calc(50% + 15px)',
    fontWeight: root.gameStore.page === 'practice' ? 800 : 400,
  }

  const menuPopupStyle = {
    position: 'absolute',
    left: '5px',
  }

  const helpPopupStyle = {
    position: 'absolute',
    right: '5px',
  }

  const NavLink = (props) => {
    return (
      <Link
        underline='hover'
        sx={props.sx}
        onClick={() => root.gameStore.setPage(props.page)}
      >
        {props.children}
      </Link>
    )
  }
  return (
    <Box id='bottomBar' sx={outerBarStyle}>
      <Box sx={innerBarStyle}>
        {root.gameStore.tutPage}
        {root.gameStore.bottomBarMode === 'singleLabel' ? (
          <Box sx={singleLabelStyle}>tutorial</Box>
        ) : (
          <Box>
            <Menu sx={menuPopupStyle} />
            <NavLink sx={dailyLabelStyle} page='daily'>
              daily
            </NavLink>
            <Typography sx={centerPipeStyle}>|</Typography>
            <NavLink sx={practiceLabelStyle} page='practice'>
              practice
            </NavLink>
            <HelpPopup sx={helpPopupStyle} />
          </Box>
        )}
      </Box>
    </Box>
  )
}

export default observer(BottomBar)
