import { useEffect, useRef, useState } from 'react'
import { Box, Fade, Typography } from '@mui/material'
import { ReactComponent as Svg } from '../assets/images/SRD_comic_clouds_11.svg'

const ChatBubble = (props) => {
  const {
    size = 300,
    bubbleFill = 'transparent',
    bubbleStroke = '#ccc',
    flipX = false,
    anchorElementId = '',
    showBubble = false,
    appearDelay = 500,
    message = '',
    fontSize = '13pt',
    justify = '',
  } = props

  const svgBoxRef = useRef()
  const [showBubbleCore, setShowBubbleCore] = useState(false)

  useEffect(() => {
    if (showBubble) {
      const anchorRef = document.getElementById(anchorElementId)
      if (anchorRef && svgBoxRef.current) {
        const anchorRect = anchorRef.getBoundingClientRect()
        const anchorLayoutParentRect = document
          .getElementById('aqua')
          .getBoundingClientRect()
        const anchorRelativeTop = anchorRect.top - anchorLayoutParentRect.top
        const bubbleRect =
          svgBoxRef.current.children[0].children[0].getBoundingClientRect()
        const top = anchorRelativeTop - bubbleRect.height
        svgBoxRef.current.style.top = `${top}px`

        switch (justify) {
          case 'right':
            svgBoxRef.current.style.right = '20px'
            svgBoxRef.current.style.left = ''
            break
          case 'left':
            svgBoxRef.current.style.left = '20px'
            svgBoxRef.current.style.right = ''
            break
          case 'center':
            svgBoxRef.current.style.left = `calc(50% - (${bubbleRect.width}px / 2))`
            svgBoxRef.current.style.right = ''
            break
          default:
            break
        }
      }
    }

    setTimeout(
      () => {
        setShowBubbleCore(showBubble)
      },
      showBubble ? appearDelay : 0
    )
  }, [props])

  const boxStyle = {
    display: 'flex',
    position: 'absolute',
    width: `${size}px`,
    height: `${size}px`,
    // background: 'lightyellow',
  }
  const textStyle = {
    position: 'absolute',
    top: '37%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    fontSize: fontSize,
    width: '80%',
    textAlign: 'center',
    // background: 'pink',
  }

  return (
    <Box sx={boxStyle} id='svgBox' ref={svgBoxRef}>
      <Fade in={showBubbleCore} timeout={{ enter: 500, exit: 50 }}>
        <Box id='svg-box-sub'>
          <Svg
            id='bubble-svg'
            width={size}
            height={size}
            fill={bubbleFill}
            stroke={bubbleStroke}
            transform={flipX ? 'scale(-1,1)' : ''}
          />
          <Typography sx={textStyle}>TESTY {message}</Typography>
        </Box>
      </Fade>
    </Box>
  )
}

export default ChatBubble
