import { useContext, useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { Box, Fade } from '@mui/material'
import {
  startGamePlay,
  selectWord,
  revealSelectedWord,
} from './gameplay/formatting'
import { sendEvent } from '../service/analytics.js'
import { RootStoreContext } from '../store/rootStoreContext'
import { puzzleWordStyles } from './quoteAreaStyle'

let pointerDownEl = null

const setupEventHandlers = () => {
  const pointerDownOnPuzzleWords = (ev) => {
    ev.preventDefault() // trying to prevent double tap to zoom
    pointerDownEl = ev.srcElement
  }
  const pointerUpOnPuzzleWords = (ev) => {
    const el = ev.srcElement
    if (el === pointerDownEl) {
      selectWord(ev.x, ev.y, el)
    }
    pointerDownEl = null
  }

  const span = document.querySelector('#sentence')
  span.addEventListener('pointerdown', pointerDownOnPuzzleWords)
  span.addEventListener('pointerup', pointerUpOnPuzzleWords)

  document.addEventListener('pointerdown', (ev) => {
    if (!ev.target.closest('#sentence') && ev.target.type !== 'button') {
      selectWord(null)
    }
  })
}

const QuoteArea = (props) => {
  const [root] = useContext(RootStoreContext)
  const [showSentence, setShowSentence] = useState(false)
  const fadeDelay = 250

  useEffect(() => {
    setupEventHandlers()
  }, [])

  useEffect(() => {
    async function initPuzzle() {
      await startGamePlay(root)
      setShowSentence(true)
      root.gameStore.setPuzzleState('playing')
      sendEvent('gameboard', 'new_puzzle')
    }

    if (root.gameStore.puzzleState === 'puzzleLoaded') {
      setShowSentence(false)
      setTimeout(() => initPuzzle(), fadeDelay)
    }
  }, [root.gameStore.puzzleState])

  useEffect(() => {
    if (root.gameStore.doRevealSelectedWord) {
      revealSelectedWord()
      root.gameStore.setDoRevealSelectedWord(false)
    }
  }, [root.gameStore.doRevealSelectedWord])

  const sentenceStyle = {
    fontSize: ['22pt', '24pt', '26pt'],
    border: '2px solid #79727044',
    borderRadius: '10px',
    width: '100%',
    py: [1, 2, 2],
    pr: [1, 2, 2],
    pl: [2, 3, 3],
    lineHeight: [1.5, 1.3, 1.2],
    userSelect: 'none',
    WebkitUserSelect: 'none',
    msUserSelect: 'none',
    WebkitTouchCallout: 'none',
    WebkitTextSizeAdjust: 'none',
    touchAction: 'manipulation' /* blocks double tap zoom */,
    fontFamily: 'Roboto',
    textAlign: 'center',
  }

  return (
    <Fade in={showSentence} appear={true} timeout={fadeDelay}>
      <Box id='sentence' sx={sentenceStyle}>
        <Box component='span' id='draggable' sx={puzzleWordStyles}></Box>
      </Box>
    </Fade>
  )
}

export default observer(QuoteArea)
